




































import { Component, Vue  } from 'vue-property-decorator';
import column from '../components/column.vue';
import Global from '../modules/Global';

@Component({
    components: {
        column
    }
})
export default class HomePage extends Vue {

    private author = Global.authorInfo;

    private pageData = Global.homeData;

    /** 视图列表 */
    get viewList() {
        let list = this.pageData.columnList
        if (this.pageData.sideIndex > 0) {
            list = list.filter(item => item.type == this.pageData.sideIndex);
        }
        return list;
    }

    /**
     * 跳转详情
     * @param id 栏目id
     */
    private openDetail(id: number) {
        // console.log('栏目id >>', id);
        this['$router'].push(`/detail/${id}`);
    }
}

